<template>
  <div>
    <div class="container" v-if="!id">
      <h4>Lista de Clientes</h4>
      <table class="table">
        <thead>
          <tr>
            <th scope="col">#</th>
            <th scope="col">Nome</th>
            <th scope="col">CPF</th>
            <th scope="col">Telefone</th>
            <th scope="col">Data de Nascimento</th>
            <th scope="col">Ações</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(client, index) in clients" :key="index">
            <th scope="row">{{ index + 1 }}</th>
            <td>
              <router-link :to="'/client/' + client.id" class="nav-link">
                {{ client.name }}
              </router-link>
            </td>
            <td>{{ client.cpf }}</td>
            <td>{{ client.phone }}</td>
            <td>{{ client.birth_date }}</td>
            <td>
              <router-link :to="'/edit/' + client.id" class="m-1 btn btn-warning">
                <font-awesome-icon icon="edit" />
              </router-link>
              <button class="m-1 btn btn-danger" @click="id = client.id">
                <font-awesome-icon icon="trash" />
              </button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div v-else>
      <h4>Você realmente deseja apagar os dados do cliente!</h4>
      <button class="btn btn-danger" @click="deleteClient(id)">
        Confirmar
      </button>
      <router-link to="/" class="btn btn-success">Voltar</router-link>
    </div>
  </div>
</template>

<script>
import ClientDataService from "../services/ClientDataService";

export default {
  name: "clients-list",
  data() {
    return {
      clients: [],
      id: null,
    };
  },
  methods: {
    retrieveClients() {
      ClientDataService.getAll()
        .then((response) => {
          this.clients = response.data.data;
          console.log(response.data);
        })
        .catch((e) => {
          console.log(e);
        });
    },

    refreshList() {
      this.retrieveClients();
    },
    deleteClient(id) {
      ClientDataService.delete(id);
      this.refreshList();
      this.id = null;
    },
  },
  mounted() {
    this.retrieveClients();
  },
};
</script>

<style>
.list {
  text-align: left;
  max-width: 750px;
  margin: auto;
}
</style>